import { createI18n } from 'vue-i18n';
import en from './en'; // 
import zhCN from './zh-CN'; // 
import { storage } from '@/utils/storage';

const config = storage.get<string>('lang'); // 
let lang = 'zh-CN';
if (config) {
	lang = config || 'zh-CN';
}
export const i18n = createI18n({
	legacy: false, // componsition APIfalse
	locale: lang,
	globalInjection: true, // template$t
	messages: {
		en,
		'zh-CN': zhCN,
	},
});
